const PendingSignContent = () => {
    const { selectedWalletConnector } = useInternalDynamicContext();
    if (selectedWalletConnector !== null) {
        if (isEmailOTPWalletConnector(selectedWalletConnector)) {
            return (jsx(EmailOTPConnectorPendingSignModalContent, { walletConnector: selectedWalletConnector }));
        }
        if (isEmailWalletConnector(selectedWalletConnector)) {
            return (jsx(EmailConnectorPendingSignModalContent, { emailWalletConnector: selectedWalletConnector }));
        }
    }
    return jsx(DefaultPendingSignModalContent, {});
};
const PendingSignWidgetPortal = () => (jsx(WidgetPortal, { onClose: useCloseMultiWalletPrompt(), children: jsx(PendingSignContent, {}) }));
const PendingSignModal = () => (jsx(PromptModal, { onClose: useCloseMultiWalletPrompt(), children: jsx(PendingSignContent, {}) }));

export { PendingSignModal, PendingSignWidgetPortal };
